.introduction-wrapper {
  display: flex;
  justify-content: space-between; /* Para que haya espacio entre las celdas */
  margin: 0 20px; /* Margen en los lados */
  max-width: 1200px; /* Ancho máximo del contenedor */
  width: 100%;
}


.introduction-left {
  flex: 1;
  padding: 20px;

  h1, p {
    text-align: left;
    padding-left: 40px;
  }

  h1 {
    font-size: 30px;
    padding-bottom: 10px;
    padding-top: 30px;
  }

  p {
    height: auto;
    max-width: 700px; /* Ajustado a un ancho máximo */
    font-size: 20px;
    padding-bottom: 30px;
  }
}

.introduction-right {
  flex: 1;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.grid-container {
  display: flex;
  flex-direction: column;
  padding: 20px; /* Ajusta según el margen deseado entre filas */
}

.grid-row {
  display: flex;
  height: 400px; /* O establece una altura mínima para la fila */
  min-height: 200px; /* Ejemplo de altura mínima, ajusta según sea necesario */
  width: 100%; 
  flex-direction: row;
  margin-bottom: 40px;

}


.grid-item {
  flex: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center; /* Para centrar la imagen y el texto */
  text-align: center;
  margin-left: 20px;
  margin-right: 20px;
}

.grid-item img {
  width: 100%; /* La imagen ocupa todo el ancho de la celda */
  height: auto; /* La altura se ajusta automáticamente para mantener la proporción */
  max-height: 100%; /* La imagen no supera la altura máxima de la celda */
}


.home-heading {
  font-size: 40px;
  font-weight: 900;
  margin-top: 80px;
  margin-bottom: 80px;
  text-align: center;
  
}